 .element__delete {
  width: 18px;
  height: 19px;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1;
  background-size: 100%;
  cursor: pointer;
  background-image: url("../../../images/button/element/elements__delete.svg");
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
  transition: opacity 0.3s ease-in;
  display: none
}

.element__delete:hover {
  opacity: 0.6;
} 


